import { Cfg } from '../Config';
import './board.scss';

const Pegs = ({ type, data }) => (
  <div className={`pegs pegs-${type}`}>
    {data.map((color, i) => (
      <div key={i} className={`peg peg-${color}`}></div>
    ))}
  </div>
);
const Code = ({ data }) => <Pegs type="code" data={data} />;
const Call = ({ data }) => <Pegs type="call" data={data} />;
const Wait = ({ data }) => (
  <div className="pegs pegs-wait">
    <button className="btn btn-outline-info" onClick={data.onClick}>
      {data.label}
    </button>
  </div>
);

const divider = Cfg.get('divider', <hr />);
const types = {
  code: Code,
  call: Call,
  wait: Wait,
};

const Row = ({ row, addDivider }) => {
  return (
    <section className="board-row">
      <div className="board-row-code" style={{ '--peg-count': Cfg.get('codes') }}>
        {Object.keys(row).map((type, i) => {
          const Type = types[type];
          return <Type key={i} data={row[type]} />;
        })}
      </div>
      {addDivider && divider}
    </section>
  );
};

const Board = ({ board }) => (
  <div className="board">
    {board.map((row, i) => (
      <Row key={i} row={row} addDivider={i < board.length - 1} />
    ))}
  </div>
);

export default Board;
