import Dropdown from 'react-bootstrap/Dropdown';
import { BiDotsVerticalRounded } from 'react-icons/bi';

// prettier-ignore
function HeaderMenu({menu = []}) {
  return (
    <Dropdown>
      <Dropdown.Toggle id="btn-settings-header" className="btn-dropdown-no-arrow" variant="" style={{fontSize: "1.5em"}}>
        <BiDotsVerticalRounded />
      </Dropdown.Toggle>

      <Dropdown.Menu align="end"    data-bs-theme="light">
        {menu.map((item, i) => 
          '-' === item.label ? 
            <Dropdown.Divider key={i} /> : 
            <Dropdown.Item key={i} href={item.url}>{item.icon} {item.label}</Dropdown.Item>
        )}
      </Dropdown.Menu>
    </Dropdown>
  );
}

export default HeaderMenu;
