/**
 * Config store.
 */
class Config {
  static opt = null;

  /**
   * Load global user config with merged defaults.
   */
  constructor(def = {}) {
    this.opt = this.opt ?? settings();
    this.opt = { ...def, ...this.opt };
    settings(this.opt);
  }

  /**
   * Set/Get config value.
   */
  cfg(key = '', val = undefined) {
    const last = this.opt[key] ?? null;

    if (undefined !== val) {
      this.opt[key] = val;
      settings(this.opt);
    }

    if ('' === key) {
      return this.opt;
    }

    return last;
  }

  /**
   * Get config value or return default.
   */
  get(key = '', def = '') {
    return this.cfg(key) ?? def;
  }

  /**
   * Merge config values.
   */
  merge(def, force = false) {
    this.opt = force ? { ...this.opt, ...def } : { ...def, ...this.opt };
    settings(this.opt);
    return this;
  }

  /**
   * Replace config values.
   */
  replace(opt) {
    this.opt = opt;
    settings(this.opt);
    return this;
  }
}

/**
 * Store/Get user settings.
 *
 * NOTE:
 * localStorage handles strings only!
 */
function settings(opt = null) {
  opt && localStorage.setItem('settings', JSON.stringify(opt));
  return JSON.parse(localStorage.getItem('settings'));
}

/*
 * ================================================================================================
 * Exports
 */
const Cfg = new Config({ isDev: 'development' === process.env.NODE_ENV });

export default Config;
export { Cfg };
