import { Cfg } from './components/Config';
import menuSettings from './js/menu-settings';
import Header from './components/Header';
import Footer from './components/Footer';
import Board from './components/Board/Board';

import './scss/bootstrap.scss';
import './App.css';

if (Cfg.get('isDev')) {
  window.Cfg = Cfg.replace({});
}

Cfg.merge({
  codes: 4,
  picks: 6,
  rows: 6,
});

function handleCheck(args) {
  console.log('handleCheck(args):', args);
}

function App() {
  const game = [
    {
      code: [3, 4, 2, 6],
      call: ['w', 'b', 0, 0],
    },
    {
      code: [5, 0, 0, 1],
      wait: { onClick: handleCheck, label: 'Label', args: { row: 2 } },
    },
    {
      code: [0, 0, 0, 0],
      call: [0, 0, 0, 0],
    },
    {
      code: [0, 0, 0, 0],
      call: [0, 0, 0, 0],
    },
  ];

  return (
    <>
      <header className="navbar bg-body fixed-top">
        <div className="container-sm">
          <Header menu={menuSettings} />
        </div>
      </header>

      <div className="container-sm">
        <Board board={game} />
      </div>

      <footer className="navbar bg-body">
        <div className="container-sm">
          <Footer />
        </div>
      </footer>
    </>
  );
}

export default App;
